import {
  CustomOverlayMap,
  Map,
  MapInfoWindow,
  MapMarker,
} from "react-kakao-maps-sdk";
import { useEffect, useState } from "react";
import styled from "styled-components";
function MapContainer({ data }: any) {
  const [state, setState] = useState<any>({
    center: { lat: 33.452613, lng: 126.570888 },
    isPanto: false,
  });
  let geocoder = new kakao.maps.services.Geocoder();

  function searchLoc() {
    geocoder.addressSearch(
      data.newAddress,
      function (result: any, status: any): any {
        //   
        if (status === kakao.maps.services.Status.OK) {
          let coords = new kakao.maps.LatLng(result[0].y, result[0].x);
          setState({ center: { lat: result[0].y, lng: result[0].x } });
        }
      }
    );
  }
  useEffect(() => {
    searchLoc();
  }, []);
  return (
    <>
      <Map //   Container
        center={state.center}
        style={{
          //  
          width: "100%",
          height: "450px",
        }}
        level={4} //   
      >
        <MapMarker //  
          position={state.center}
        ></MapMarker>
        <CustomOverlayMap position={state.center} xAnchor={0.5} yAnchor={1.5}>
          <Div>
            <div className="building">{data.building}</div>
            <div className="buttons">
              <div className="road"></div>
              <div className="showmap"></div>
            </div>
          </Div>
        </CustomOverlayMap>
      </Map>

      <button></button>
    </>
  );
}

const Div = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: auto;
  height: auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 0px 20px 10px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  & .buttons {
    display: flex;
  }
  & .building {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    height: 50px;
    padding-top: 10px;
    font-weight: bolder;
    font-size: 1.3rem;
  }
  & .road,
  .showmap {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 71, 71);
    color: white;
    width: 100px;
    height: 40px;
    border-radius: 10px;
    margin: 10px;
  }

  & .showmap {
    margin-left: 0px;
  }
`;

export default MapContainer;
